  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: 250vh 100vh;
  background-color: rgba(217, 235, 225, 0.6)

  
}

.container {
  max-width: 66vh;
  margin: 1px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 1px;
  border-radius: 5px;
  background-color: #3c826ea4;
}

.header {
