}

.form-control input {
  width: 100%;
  height: 40px;
  margin: 10px;
  padding: 3px 7px;
  font-size: 15px;
}

.form-control-check {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control-check label {
  flex: 1 1;
}

.form-control-check input {
  flex: 2 1;
  height: 20px;
}

footer {
