}

@-webkit-keyframes particleAnimation
{
    from {
        top: -100px;
    }
  
  50% {
    -webkit-transform: translateY(500px);
            transform: translateY(500px);
  }
    to {
        left: calc( 100% + 100px );
        top: calc( 100% + 100px );
    }
}

@keyframes particleAnimation
{
    from {
        top: -100px;
    }
  
  50% {
    -webkit-transform: translateY(500px);
            transform: translateY(500px);
  }
    to {
        left: calc( 100% + 100px );
        top: calc( 100% + 100px );
    }
}

@-webkit-keyframes tothetop
{
  
  from {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background: #219c5c;
  }
  
  to {
    display: block;
    align-items: none;
    justify-content: none;
    min-height: 0vh;
    align-items: center;
  }
}

@keyframes tothetop
{
  
  from {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  background: #219c5c;
  }
  
  to {
    display: block;
    align-items: none;
    justify-content: none;
    min-height: 0vh;
    align-items: center;
  }
}

@-webkit-keyframes moonanim
{
  from {
    top: -50vh;
  }
  
  to {
    top: 0;
  }
}

@keyframes moonanim
{
  from {
    top: -50vh;
  }
  
  to {
    top: 0;
  }
}

