.glass {
    position: relative;
    background: rgba(0, 0, 0, 0.05);
    border-radius: 6px;
    overflow: hidden;
    z-index: 10;
    -webkit-backdrop-filter: blur(15px);
    backdrop-filter: blur(15px);
    border-top: 1px solid rgba(225, 225, 225, 0.2);
    border-left: 1px solid rgba(225, 225, 225, 0.2);
    box-shadow: 5px 5px 30px rgba(0, 0, 0, 0.2);
    min-width: 35vw;
    padding: 20px 30px;
    display: flex;
  }

  .glass__inputs {
    float: left;
  }

  .glass__output {
    position: relative;
    left: 10px; 
    top: 100px;
    float: right;
    width: 80%;
    /* height: 100%; */
    min-width: 25em;
    max-width: 25em;
    max-height: 30em;
    padding:  10px 10px;
    border-radius: 3px;
    box-shadow: 5px 5px 5px 5px rgb(0 0 0 / 10%);
    offset-position: center;
    /* width: 100%; */
  }

  .glass__output p {
    font-family: sans-serif;
    text-align: center;
    margin: 10px;
    color: #000;
    position: relative;
  }
  
  .glass__form {
    position: relative;
    display: grid;
  }
  
  .glass__form p {
    font-family: sans-serif;
    text-align: left;
    margin: 10px;
    color: #000;
    font-size: 13px;
  }
  
  .glass__form__group {
    margin: 10px 0;
  }
  
  .glass__form__input {
    width: 90%;
    background: transparent;
    outline: none;
    border: none;
    padding: 10px;
    border-radius: 3px;
    box-shadow: 5px 5px 5px 5px rgb(0 0 0 / 10%);
  }
  
  .glass__form__input:focus {
    background: rgba(255, 255, 255, 0.5);
  }
  
  .glass__form__btn {
    display: inline-block;
    background: #000;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 5px;
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    font-size: 15px;
    font-family: inherit;
    border: 1px solid #000;
    outline: none;
  }
  
  .glass__form__btn:hover {
    background: transparent;
    color: #000000;
    transition: all ease-in-out 0.3s;
  }
  
  ::-webkit-input-placeholder {
    /* WebKit, Blink, Edge */
    color: #000;
  }
  
  ::placeholder {
    /* Most modern browsers support this now. */
    color: #000;
  }
